.popup__container {
  position: fixed;
  z-index: 5;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 25px 22px;
  margin: 0 auto;
  width: 282px;
  height: 322px;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  box-sizing: border-box;
}

@media screen and (min-width: 768px) {
  .popup__container {
    width: 350px;
    height: 330px;
  }
}

@media screen and (min-width: 1280px) {
  .popup__container {
    width: 430px;
  }
}